body {
  background: url("https://images.ctfassets.net/hrltx12pl8hq/2RwJp3f9UiCnfWBEunwxOQ/f11257994853124d7b1a6a935e678c13/0_hero.webp");
  padding: 20px;
  font-family: Arial, sans-serif;
}

.title {
  color: #a2d3a6;
  text-align: center;
}

.search-form input {
  background-color: #3c463e;
  border: 1px solid #3c463e;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: 18px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 8px #0000001a;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.stats {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.stats-item {
  align-items: center;
  font-size: 14px;
  display: flex;
}

.material-icons {
  margin-right: 5px;
}

#load-more {
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}
